t2 .flex-section {
    background: var(--white);
    padding: 2rem;
}

.t2 .flex-section .container {
    max-width: unset !important;
    overflow: hidden;
}

.t2 .flex-section .container div.row {
    padding-bottom: 1em;
    margin-right: 0;
    margin-left: 0;
    margin-bottom: 4px;
}

.svc-catalog-section ul.link-anchor,
.t2 .flex-section ul.nav-links {
    min-height: 100%;
    display: flex;
    border-bottom: 4px solid var(--white);
    border-right: 4px solid var(--white);
}


.t2 .flex-section ul.nav-links,
.t2 .flex-section ul.nav-links > li ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.degrees {
    border-bottom: 2px solid var(--white);
    margin-bottom: 0;
    &:last-child() {
        border-bottom: 0;
    }
}


.t2 .flex-section ul.nav-links > li > a {
    background: rgba(0, 0, 0, 0);
    color: #fff;
    padding: 1.2em 18px .7em;
}

.t2 .flex-section ul.nav-links > li ul > li a {
    padding: 1.2em 10% .7em;
}

.t2 .flex-section ul.nav-links:nth-of-type(3) > li a {
    min-height: 100%;
}

.t2 .flex-section ul.nav-links:nth-of-type(3) > li a:hover {
    background: rgba(0, 0, 0, .3);
}

.t2 .flex-section ul.nav-links > li ul > li a,
.t2 .flex-section ul.nav-links > li ul > li span {
    font-size: 14px;
    font-weight: 400;
    height: 100%;
}

.t2 .flex-section ul.nav-links > li ul > li span {
    color: #ccc;
}

.t2 .flex-section ul.nav-links > li ul > li:last-child a {
    padding-bottom: 1.5em;
}


@media screen and (max-width:768px) {
    .svc-catalog-section ul.link-anchor,
    .t2 .flex-section ul.nav-links {
        border-right: 0;
    }
}


@media screen and (min-width:768px) {
    .t2 .flex-section ul.nav-links:first-of-type,
    .t2 .flex-section ul.nav-links:nth-of-type(5) {
        border-left: 4px solid #fff;
    }
}

.t2 .flex-section ul.nav-links > li {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -moz-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.t2.page-centers-institutes .flex-section ul.nav-links > li span {
    display: block;
    font-weight: 600;
    padding: 1.2em 10% .7em;
    color: #f0e1b0;
}

.svc-catalog-section ul.link-anchor li {
    flex: 1 1 50%;
    min-width: fit-content;
    align-self: center;
    text-align: center;
    margin: 0;
    border-bottom: 2px solid #fff;
    border-right: 0;
}

@media screen and (min-width:768px) {
    .svc-catalog-section ul.link-anchor li {
        border-right: 2px solid #fff;
    }
}

@media screen and (min-width:992px) {
    .svc-catalog-section ul.link-anchor li {
        flex: 1 1 33%;
    }
    .svc-catalog-section ul.link-anchor li a {
        padding: 2em 10%;
    }
}


.t2 .flex-section ul.nav-links > li ul > li {
    background: rgba(0, 0, 0, .2);
}


.page-service-catalog ul.link-anchor li a,
.t2 .flex-section ul.nav-links > li ul > li a,
.t2 .flex-section ul.nav-links > li ul > li span {
    display: block;
    width: 100%;
    padding: 3rem 10%;
    color: var(--white);
    font-size: 18px;
    line-height: 1;
    font-weight: 600;
    text-shadow: var(--hero-dropshadow);
    background: rgba(0, 0, 0, .3);
    /*max-height: fit-content;*/
}


.svc-catalog-section ul.link-anchor li a:link,
.svc-catalog-section ul.link-anchor li a:visited,
.t2 .flex-section ul.nav-links > li ul > li a:link,
.t2 .flex-section ul.nav-links > li ul > li a:visited,
.degrees > div > a:link,
.degrees > div > a:visited {
    text-shadow: rgba(0, 0, 0, .5) 1px 1px 0;
    background: rgba(0, 0, 0, .3);
}

.svc-catalog-section ul.link-anchor li a:hover,
.svc-catalog-section ul.link-anchor li a:focus,
.svc-catalog-section ul.link-anchor li a:active,
.t2 .flex-section ul.nav-links > li ul > li a:hover,
.t2 .flex-section ul.nav-links > li ul > li a:active,
.t2 .flex-section ul.nav-links > li ul > li a:focus,
.degrees > div > a:hover,
.degrees > div > a:active,
.degrees > div > a:focus {
    text-decoration: none;
    text-shadow: rgba(0, 0, 0, .8) 1px 1px 0;
    background: rgba(0, 0, 0, .7);
}

.t2 .flex-section ul.nav-links > li ul > li a:hover,
.t2 .flex-section ul.nav-links > li ul > li a:active,
.t2 .flex-section ul.nav-links > li ul > li a:focus {
    color: #e6b711;
}

.page-service-catalog ul.link-anchor li {
    background-color: rgb(75, 121, 67);
}

.page-service-catalog ul.link-anchor li:nth-child(2),
.degrees.row div:first-child,
.degrees div:nth-child(7),
.degrees div:nth-child(13) {
    background-color: #55ba47;
}

.page-service-catalog ul.link-anchor li:nth-child(3) {
    background-color: rgb(170, 152, 94);
}

.page-service-catalog ul.link-anchor li:nth-child(4),
.degrees.row div:nth-child(6n+2) {
    background-color: #c4b581;
}

.degrees.row div:nth-child(6n+3) {
    background-color: #004e38;
}

.page-service-catalog ul.link-anchor li:nth-child(6),
.page-service-catalog ul.link-anchor li:nth-child(8),
.degrees div:nth-child(6n+4) {
    background-color: #008554;
}

.page-service-catalog ul.link-anchor li:nth-child(7),
.page-service-catalog ul.link-anchor li:nth-child(9),
.degrees div:nth-child(6n+5) {
    background-color: #d9d0ac;
}

//.degrees.row div:nth-child(6n) {
//    background-color: #00aa4f;
//}
.page-service-catalog ul.link-anchor li:nth-child(5) {
    background-color: rgb(0, 78, 56);
}

.page-service-catalog ul.link-anchor li:nth-child(8) {
    background-color: rgb(0, 170, 79);
}

.t2 .flex-section ul.nav-links:nth-child(odd) {
    background-color: rgb(0, 78, 56);
}

.t2 .flex-section ul.nav-links:nth-child(even) {
    background-color: rgb(0, 46, 35);
}



@media screen and (min-width:768px) {
    .t2 .flex-section ul.nav-links.col-md-5 > li ul {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
    }
    .t2 .flex-section ul.nav-links.col-md-5 > li ul li {
        flex: 0 0 50%;
    }
    .t2 .flex-section ul.nav-links.col-md-5 li ul li:nth-of-type(even) {
        border-left: 1px solid #008554;
    }
}

.t2 .flex-section ul.nav-links li ul li {
    border-bottom: 1px solid #008554;
}

.t2 .flex-section ul.nav-links:first-of-type li ul li:last-child {
    border-bottom: 0 !important;
}











// T2 Colleges ??? ///
ul {
    &.degreeswrap {
        padding: 0;
        margin: 0 auto;
        max-width: 1140px;
        position: relative;
        list-style: none;
        border-bottom: 2px solid #fff;
        border-right: 2px solid #fff;
        ul {
            &.degrees {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                list-style: none;
                max-width: 1140px;
                padding-left: 0;
                margin: 0 auto;
                border-right: 2px solid #fff;
                border-bottom: 2px solid #fff;
                li {
                    -webkit-box-flex: 0;
                    -ms-flex: 0 1 100%;
                    flex: 0 1 100%;
                    min-width: -webkit-min-content;
                    min-width: -moz-min-content;
                    min-width: min-content;
                    -webkit-box-pack: center;
                    -ms-flex-pack: center;
                    justify-content: center;
                    -webkit-box-align: center;
                    -ms-flex-align: center;
                    align-items: center;
                    border-left: 2px solid #fff;
                    border-top: 2px solid #fff;
                    @media screen and (min-width: 600px) {
                        flex: 1 0 50%;
                    }
                    @media screen and (min-width:900px) {
                        flex: 1 0 25%;
                        min-width: min-content;
                    }
                    a {
                        text-align: center;
                        display: block;
                        padding: 2rem 5%;
                        -webkit-transition: all 0.5s ease;
                        transition: all 0.5s ease;
                        color: #043927;
                        background: rgba(255, 255, 255, .3);
                        text-align: center;
                        text-decoration: none;
                        height: 100%;
                        &:visited {
                            color: #333;
                        }
                        &:hover {
                            color: #008554;
                            background: rgba(0, 0, 0, .1);
                            /*text-shadow: 1px 1px 0 #ffffc9, -1px -1px 0 #ffffc9, -1px 1px 0 #ffffc9, 1px -1px 0 #ffffc9;*/
                        }







                    }
                }
            }
        }
    }
}


@media screen and (min-width:900px) {
    section.degrees-majors-section {
        background-color: rgb(194, 253, 219);
        background-image: url('../svg/nav-dots.svg'), linear-gradient(15deg, rgb(194, 253, 219) 0%, rgb(234, 253, 246) 50%, rgb(194, 253, 219) 100%);
    }
    .t2 {
        .page-class-schedules,
        .page-housing,
        .page-arts-letters,
        .page-social-sciences-interdisciplinary-studies,
        .page-administration-business-affairs {
            ul.degrees li {
                flex: 1 0 20%;
            }
        }
        .page-health-human-services {
            ul.degrees li {
                -webkit-box-flex: 1;
                -ms-flex: 1 0 25%;
                flex: 1 0 25%;
            }
        }
        .page-engineering-computer-science,
        .page-natural-sciences-mathematics,
        .page-business-administration,
        .page-information-resources-technology {
            ul.degrees li {
                -webkit-box-flex: 1;
                flex: 1 0 33.3333%;
            }
        }
        .page-education {
            ul.degrees li {
                -webkit-box-flex: 0;
                -ms-flex: 0 1 50%;
                flex: 0 1 50%;
            }
        }
    }
}
